.lenses-landing {
  background-color: #114174;
  position: relative;
  overflow: hidden;
  &__bg {
    position: absolute;
    @media (max-width: 1199px) {
      display: none;
    }
    &--1 {
      top: 3125px;
      right: 0;
    }
    &--2 {
      top: 3525px;
      left: 0;
    }
    &--3 {
      top: 5425px;
      right: calc(50% - 900px);
    }
    &--4 {
      top: 6525px;
      left: 20px;
    }
    &--5 {
      top: 6275px;
      right: 0;
    }
  }
  .rel-wrapper {
    position: relative;
    z-index: 1;
  }
}

.container {
  max-width: 1220px;
  padding: 0 10px;
}

.main-banner {
  padding: 190px 0 140px;
  position: relative;
  @media (max-width: 991px) {
    padding: 80px 0 60px;
  }

  &__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__container {
    position: relative;
    z-index: 1;
    max-width: 1420px;
    padding: 0 10px;
  }

  &__border {
    border: 1px solid #FFFFFF;
    border-radius: 30px;
    padding: 80px 75px 108px;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 46px;
    @media (max-width: 1364px) {
      justify-content: flex-start;
    }
    @media (max-width: 991px) {
      padding: 30px 20px;
    }
  }

  &__img1 {
    position: absolute;
    left: 42px;
    top: 99px;
    animation-duration: 9.2s;
    animation-name: lense1;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    @media (max-width: 1364px) {
      display: none;
    }
  }

  &__img2 {
    position: absolute;
    left: 282px;
    top: -42px;
    animation-duration: 9s;
    animation-name: lense2;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    @media (max-width: 1364px) {
      display: none;
    }
  }

  &__content {
    max-width: 616px;
    flex-basis: 616px;
  }

  &__subtitle {
    color: #fff;
    font-size: 32px;
    line-height: 31px;
    margin-bottom: 35px;
    @media (max-width: 991px) {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 20px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 59px;
    line-height: 45px;
    text-transform: uppercase;
    color: #fff;
    @media (max-width: 991px) {
      font-size: 26px;
      line-height: 26px;
      display: flex;
      flex-direction: column;
    }

    span {
      font-size: 42px;
      line-height: 40px;
      display: inline-block;
      margin-bottom: 5px;
      @media (max-width: 991px) {
        font-size: 18px;
        line-height: 18px;
        margin: 5px 0;
      }
    }
  }

  &__bottom {
    padding: 0 75px;
    display: flex;
    justify-content: flex-end;
    @media (max-width: 1364px) {
      justify-content: flex-start;
    }
    @media (max-width: 991px) {
      padding: 0 20px;
    }
    @media (max-width: 600px) {
      br {
        display: none;
      }
    }
  }

  &__bottom-box {
    max-width: 616px;
    flex-basis: 616px;
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: #FFFFFF;
    margin-bottom: 30px;
    @media (max-width: 991px) {
      font-size: 16px;
      line-height: 21px;
      margin-bottom: 10px;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;

    a {
      font-size: 18px;
      line-height: 20px;
      color: #8CC9FF;
      text-decoration: underline;
      @media (max-width: 991px) {
        font-size: 16px;
        line-height: 21px;
      }

      &:hover {
        text-decoration-color: transparent;
      }
    }
  }
}

@keyframes lense1 {
  from {
    transform: translateY(0)
  }
  50% {
    transform: translateY(70px)
  }
  to {
    transform: translateY(0)
  }
}

@keyframes lense2 {
  from {
    transform: translateY(0)
  }
  50% {
    transform: translateY(-70px)
  }
  to {
    transform: translateY(0)
  }
}


