        &__bg-light {
          opacity: 1;
          transition: 1s ease-in-out;
          filter: brightness(3) contrast(0.5);
        }

        &__bg-lens {
          opacity: 1;
          transform: translateX(-50%) scale(1.2);
          transition: 1s ease-in-out;
        }

        &__items-col:first-child {
          .advantages-landing__item {
            &:first-child,
            &:nth-child(3) {
              transform: translateX(-55px);
              transition: 1s ease-in-out;
