    overflow-y: hidden;
    border-radius: 15px;
    position: relative;
    padding-bottom: 35px;
  }

  .read-more-toggle {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: 61px;
    height: 67px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #48A0E8;
    border-radius: 10px;
    z-index: 1;
    cursor: pointer;

    &.opened {
      svg {
        transform: rotate(180deg);
        transition: .6s ease-in-out;
      }
    }

    svg {
      width: 20px;
      height: 20px;
      fill: transparent;
      transition: .6s ease-in-out;
    }
  }

  .read-more-bg {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 70px;
    background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0, #114174 50%);
  }
}

.price-landing-table {
  border-collapse: collapse;

  th {
    background-color: $white;
    font-size: 18px;
    line-height: 24px;
    color: #114174;
    padding: 15px 35px;
    border: 0;
    @media (max-width: 767px) {
      font-size: 14px;
      line-height: 19px;
      padding: 15px 10px;
    }

    &:first-child {
      border-radius: 15px 0 0 15px;
    }

    &:last-child {
      border-radius: 0 15px 15px 0;
    }
  }

  thead {
    position: relative;
    z-index: 1;
  }

  tbody {
    position: relative;
    bottom: 10px;
  }

  td {
    background-color: #0D345F;
    font-size: 18px;
    line-height: 24px;
    color: $white;
    padding: 21px 35px;
    border: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    @media (max-width: 767px) {
      font-size: 14px;
      line-height: 19px;
      padding: 15px 10px;
    }

    &:last-child {
      white-space: nowrap;
    }
  }

  tbody {
    tr:first-child {
      td {
        padding-top: 40px;
        @media (max-width: 767px) {
          padding-top: 30px;
        }
      }
    }

    tr:last-child {
      td {
        border-bottom: 0;

        &:first-child {
          border-radius: 0 0 0 15px;
        }

        &:last-child {
          border-radius: 0 0 15px 0;
        }
      }
    }
  }
}

.doctors-landing {
  padding-bottom: 170px;
  position: relative;
  @media (max-width: 600px) {
    padding: 40px 0 80px;
  }

  &__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__container {
    position: relative;
    z-index: 1;
  }

  .doctor__item {
    @media (max-width: 440px) {
      margin: 0 -10px 0;
    }

    p {
      font-family: Open Sans, sans-serif;
      line-height: 22px;
      margin-top: 0;
      margin-bottom: 35px;
    }

    h3 {
      margin-bottom: 20px;
    }

  }

  .doctor__img {
    img {
      @media (max-width: 767px) {
        height: auto;
      }
    }
  }
}

.testimonials-landing {
  padding-bottom: 200px;
  background-image: radial-gradient(50% 50% at 50% 50%, #00D1FF 0%, rgba(0, 209, 255, 0) 100%);
  background-position-y: 500px;
  background-repeat: no-repeat;
  @media (max-width: 600px) {
    padding: 40px 0 80px;
  }

  &__slider {
    overflow: hidden;
    position: relative;
    padding: 0 30px;
    width: calc(100% + 60px);
    @media (max-width: 1365px) {
      width: 100%;
    }
    @media (max-width: 767px) {
      padding: 0;
    }
  }

  &__item {
    background-color: $white;
    padding: 85px 105px;
    border-radius: 30px;
    @media (max-width: 767px) {
      padding: 40px 15px;
    }
  }

  &__item-top {
    display: flex;
    align-items: center;
    margin-bottom: 45px;
    @media (max-width: 767px) {
      flex-direction: column;
      margin-bottom: 20px;
      align-items: flex-start;
    }
  }

  &__name {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: #025DA8;
    margin-right: 20px;
    @media (max-width: 767px) {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &__date {
    font-size: 16px;
    line-height: 19px;
    font-weight: 700;
  }

  &__text {
    margin-bottom: 50px;
    font-family: 'Gotham Pro', sans-serif;

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 21px;
      margin-bottom: 20px;
      font-family: 'Gotham Pro', sans-serif;
      @media (max-width: 767px) {
        font-size: 14px;
        line-height: 19px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__doctor {
    display: flex;
    align-items: center;

    img {
      width: 51px;
      height: 51px;
      margin-right: 14px;
    }
  }

  &__doctor-content {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
  }

  &__doctor-name {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: #025DA8;
    margin-top: 3px;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    width: 60px;
    height: 60px;
    border-radius: 10px;
    background-color: #48A0E8;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    cursor: pointer;
    transition: .3s ease-in-out;
    transform: translateY(-50%);
    @media (max-width: 767px) {
      display: none;
    }

    svg {
      fill: transparent;
      width: 20px;
      height: 20px;
    }

    &:hover {
      box-shadow: 0 13px 43px rgba(0, 117, 255, 0.5);
      transition: .3s ease-in-out;
    }

    &--prev {
      left: 0;

      svg {
        transform: scale(-1);
      }
    }

    &--next {
      right: 0;
    }
  }
}